.App-header {
  background-color: #222;
  height: 50px;
  padding: 20px;
  color: white;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.App-header a {
  color: white;
  text-decoration: none;
}

.App-wrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  margin-top: 10px;
}

.app-cart {
  font-size: 2rem;
}

.App-header .add-product {
  background: aliceblue;
  color: black;
  margin-left: auto;
  margin-right: 30px;
  border-radius: 5px;
  padding: 10px;
  font-weight: bold;
}



