.App-sidebar {
  display: -ms-flexbox;
  display: flex;
  min-width: 150px;
  height: 100%;
  background: #222;
  padding: 0 10px;
}

.App-sidebar-nav a {
  display: block;
  padding: 10px 0;
  cursor: pointer;
  color: #fff;
  text-decoration: none;
}

.App-sidebar-nav .active {
  text-decoration: underline;
}



