.App-product_list {
  width: 100%;
  padding: 0 10px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-align: start;
      align-items: flex-start;
  -ms-flex-line-pack: start;
      align-content: flex-start;
}

.product_list_item {
  display: block;
  padding: 20px;
  border: 1px solid #eee;
  border-radius: 3px;
  margin: 10px;
}

.product_list_item p {
  font-size: 0.9em;
  line-height: 21px;
  margin: 0;
}

.add-to-cart-btn {
  margin-top: 10px;
}

.select-container {
  padding: 0 10px;
}

.sort {
  margin-right: 10px;
}



